.bContent {
    padding: 0 1rem;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.bContent p {
    padding: 1rem;
    max-width: 1000px;
}

.bContent__empty {
    display: block;
    width: 100%;
    height: 150px;
    background: url('../../nothing_here.svg') left center no-repeat;
    
}

.bContent span[role=img] {
    display: inline-block;
    margin-right: 1em;
}

.bContent__header {
    font-weight: 500;
    font-size: 2.25rem;
    padding-bottom: 10px;
}


