.bButtonsGroup__button {
    font-size: 14px;
    font-weight: 500;
    background-color: transparent;
    border: 2px solid #a9649e;
    color: #a9649e;
    padding: 7px 14px;
    cursor: pointer;
    float: left;
}

.bButtonsGroup:after {
    content: "";
    clear: both;
    display: table;
}

.bButtonsGroup__button:not(:last-child) {
    border-right: none; 
}

.bButtonsGroup__button:first-of-type {
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
}

.bButtonsGroup__button:last-of-type {
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
}

.bButtonsGroup__button:hover {
    background: rgba(169,100,158, .25);
}

.bButtonsGroup__button--active {
    background: #a9649e;
    color: #fff;
}
.bButtonsGroup__button--active:hover {
    background: #a9649e;
    color: #fff;
}




