.bPeriodSelector {
    padding: 1rem 0;
    border-bottom: 1px solid #eee;
}

.bPeriodSelector__link {
    text-decoration: none;
    cursor: pointer;
    color: #a9649e;
    border-bottom: 2px solid rgba(169,100,158, .25);
    padding: 3px 0;
}
.bPeriodSelector__link--active {
    color: #fff !important;
    background-color: #a9649e;
    border-radius: 4px;
    padding: 5px 7px;
    border: 0;
}


